/******************************************************************

Stylesheet: ベーススタイル

******************************************************************/
/* global.cssの追加CSSと衝突しないようにh2タグにクラス付与 */
.App .practiceCode,
.App .completeCode {
  padding-top: 30px;
  text-align: center;
}

/* オプション */
.App-select {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  font-size: 1.05em;
}

/* 追加 : ラジオボタン */
.App-screen {
  position: fixed;
  top: 0;
  right: 1em;
  z-index: 3;
  flex: 1.05em;
}

/* start、endを選択の場合は高さを100vh */
.App-start,
.App-end {
  height: 100vh;
  overflow-y: scroll;
  padding-left: 16px;
  padding-right: 16px;
  padding-bottom: 16px;
  position: relative;
}

/* 追加 : bothを選択の場合は高さを50vh */
.App .content-both .App-start,
.App .content-both .App-end {
  height: 50vh;
}

.App-start {
  background: #cffafe;
}

.App-end {
  /* background: #fef9c3; */
}

/******************************************************************
追加 : 画面表示切り替えのCSS
******************************************************************/
/* startを選択 → 完成コードを消す */
.App .content-start .App-end {
  display: none;
}

/* endを選択 → 練習コードを消す */
.App .content-end .App-start {
  display: none;
}

/******************************************************************

Stylesheet: 600px以上のタブレットやモニタで適用

******************************************************************/
/* 追加 : both:両方を選択した場合のみフレックスで横並び */
@media (min-width: 600px) {
  .App .content-both {
    display: flex;
  }

  .App .content-both .App-start,
  .App .content-both .App-end {
    flex: 0 0 50%;
    min-height: 100vh;
  }
}
