/****************************************************************

色など

*****************************************************************/
:root{
  /* 基本色 */
  --text: #454A55;
  --blue: #28759F;
  --red: red;
  --white: white;

  /* 背景色 */
  --bgBeige: #F6EEDD;
  --bgLightBeige: #FBF7ED;
  --bgGray: #D9D9D9;           /* キャンセル、一覧へボタン */
  --bgDelete: #B5A981;      /* 削除ボタン */
  
  --starYellow: #FFD233;    /* 星 */
  --starBeige: #E4DCCB;     /* 星(クリックなし) */

  /* ボックスシャドウ */
  --shadowSp: 0 4px 10px rgba(0, 0, 0, .1);
  --shadowPc: 0 4px 10px rgba(0, 0, 0, .2);
}

/****************************************************************

フォントファミリー、リセットCSSなど

*****************************************************************/
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;500;700&display=swap");

/* bodyは上書き index.cssにデフォルトで記載あり */
body{
  font-family: 'Noto Sans JP', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;

  background-color: var(--bgLightBeige);
}

* {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

ul{
  padding: 0;
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  width: 100%;
}

a {
  color: inherit;
  text-decoration: none;
}

/****************************************************************

フォーム

*****************************************************************/
label{
  display: block;
}

input[type="text"],
button,
textarea{
  appearance: none;
  background: transparent;
  background-image: none;
  border: none;
  border-radius: 0;
  font-family: inherit;
  outline: none;
  margin: 0;
  padding: 0;
}

input[type="submit"],
input[type="button"],
label,
button {
  cursor: pointer;
}

/* テキスト */
input[type="text"]{
  padding-top: 14px;
  padding-bottom: 14px;
  padding-right: 21px;
  padding-left: 21px;
  width: 100%;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  color: var(--text);
	background-color: rgba(255, 255, 255, .6);
	border: 2px solid #E4DCCB;
	border-radius: 5px;
  appearance: none;
}

/* テキストエリア */
textarea{
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 15px;
  padding-left: 15px;
  width: 100%;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  color: var(--text);
	background-color: rgba(255, 255, 255, .6);
	border: 2px solid #E4DCCB;
	border-radius: 5px;
  appearance: none;
}

@media screen and (min-width: 1660px){
  input[type="text"]{
    padding-top: 17px;
    padding-bottom: 17px;
    padding-right: 33px;
    padding-left: 33px;
    font-size: 18px;
    border: 5px solid #E4DCCB;
    border-radius: 10px;
  }
  
  /* テキストエリア */
  textarea{
    padding-top: 22px;
    padding-bottom: 22px;
    padding-right: 30px;
    padding-left: 30px;
    font-size: 18px;
    border: 5px solid #E4DCCB;
    border-radius: 10px;
  }
}

/****************************************************************

header

*****************************************************************/
.header{
  height: 61px;
}

.header__inner{
  height: inherit;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.header__logo{
  height: inherit;
}

.header__logo a{
  height: inherit;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.header__logo a img{
  width: 80px;
}

.header__nav{
  height: inherit;
}

.header__ul{
  height: inherit;
  display: flex;
  align-items: center;
}

.header__list{
  height: inherit;
}

.header__list a{
  padding-right: 12px;
  padding-left: 12px;
  height: inherit;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  color: var(--blue);
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.header__list:not(:first-child) a{
  padding-right: 0;
}

@media screen and (min-width: 1660px){
  .header{
    height: 208px;
  }

  .header__logo a img{
    width: 300px;
  }
  
  .header__list a{
    padding-right: 52px;
    padding-left: 52px;
    font-size: 32px;
  }
}

/****************************************************************

共通コンポーネント

*****************************************************************/
/* ページタイトル h1 */
.pageTitle{
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  color: var(--text);
  text-align: center;
}

.btn{
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-right: 16px;
  padding-left: 16px;
  width: 100%;
  max-width: 150px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  color: var(--white);
  text-align: center;
  border-radius: 5px;
  transition: opacity .3s;
}


@media screen and (min-width: 1660px){
  .pageTitle{
    font-size: 36px;
  }

  .btn{
    padding-top: 18px;
    padding-bottom: 18px;
    padding-right: 18px;
    padding-left: 18px;
    max-width: 200px;
    font-size: 24px;
    border-radius: 5px;
  }

  .btn:hover{
    opacity: .6;
  }
}


/****************************************************************

フォーム  新規投稿のフォームと編集モーダルを共通化

*****************************************************************/
.form{
  margin-top: 16px;
  margin-right: auto;
  margin-left: auto;
  padding-top: 26px;
  padding-bottom: 27px;
  padding-right: 10px;
  padding-left: 10px;
  width: 100%;
  max-width: 600px;
  background-color: #F7EEDD;
  border-radius: 5px;
  box-shadow: var(--shadowSp);
}

/* 編集モーダルの背景色を変える */
.editModal .form{
  margin-top: 0;
  margin-right: 12px;
  margin-left: 12px;
  background-color: var(--bgLightBeige);
}

/* エラーメッセージ */
.required{
  margin-top: 5px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: var(--red);
}

.form__title{
  margin-bottom: 8px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  color: var(--text);
}

/* 編集モーダルのタイトル */
.form__titleEdit{
  margin-bottom: 22px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  color: var(--text);
  text-align: center;
}
.form__titleEdit span{
  font-size: 14px;
}

.form__description{
  margin-top: 24px;
  margin-bottom: 8px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  color: var(--text);
}

.form__comment{
  margin-top: 24px;
  margin-bottom: 8px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  color: var(--text);
}

.form__rating{
  margin-top: 24px;
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  color: var(--text);
}

/* React Raringの星 */
.form__stars svg{
  width: 33px;
  height: 33px;
}

.form__btn{
  margin-top: 41px;
  text-align: center;
}

/* 編集モーダルのボタン キャンセルと確定する */
.form__btnEdit{
  margin-top: 41px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.form__btnEdit .btn:first-child{
  margin-right: 16px;
}

@media screen and (min-width: 1660px){
  .form{
    margin-top: 40px;
    padding-top: 78px;
    padding-bottom: 78px;
    padding-right: 150px;
    padding-left: 150px;
    max-width: 1200px;
    border-radius: 10px;
    box-shadow: var(--shadowPc);
  }
  
  /* エラーメッセージ */
  .required{
    margin-top: 5px;
    font-size: 18px;
  }
  
  .form__title{
    margin-bottom: 8px;
    font-size: 24px;
  }

  /* 編集モーダルのタイトル */
  .form__titleEdit{
    margin-bottom: 29px;
    font-size: 32px;
  }
  .form__titleEdit span{
    font-size: 24px;
  }

  
  .form__description{
    margin-top: 24px;
    margin-bottom: 8px;
    font-size: 24px;
  }
  
  .form__comment{
    margin-top: 24px;
    margin-bottom: 8px;
    font-size: 24px;
  }
  
  .form__rating{
    margin-top: 24px;
    margin-bottom: 5px;
    font-size: 24px;
  }
  
  .form__btn{
    margin-top: 41px;
    text-align: center;
  }

  /* 編集モーダル */
  .form__btnEdit .btn:first-child{
    margin-right: 32px;
  }
}


/****************************************************************

カード

*****************************************************************/
/* カードのコンテナ */
.cards{
  margin-top: 16px;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  max-width: 343px;

  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

/* カード */
.card{
  display: block;
  padding-top: 34px;
  padding-bottom: 34px;
  padding-right: 13px;
  padding-left: 13px;
  width: 100%;
  max-width: 343px;
  background-color: var(--bgBeige);
  border-radius: 5px;
  box-shadow: var(--shadowSp);
  position: relative;
}

/* タイトル */
.card__title{
  font-weight: 700;
  font-size: 18px;
  line-height: 1.5;
  color: var(--blue);
}

/* 取得してきた概要、感想 */
.card__text{
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: var(--text);
}

/* 概要 */
.card__description{
  margin-top: 18px;
  margin-bottom: 8px;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.5;
  color: var(--text);
}

/* 感想 */
.card__comment{
  margin-top: 16px;
  margin-bottom: 8px;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.5;
  color: var(--text);
}

/* 星 */
.card__rating{
  margin-top: 16px;
}

/* React Ratingの星 */
.card__rating svg{
  width: 16px;
  height: 16px;
}


@media screen and (min-width: 768px){
  .cards{
    max-width: 706px;
  }

  .card{
    max-width: calc(50% - 10px);
  }
}

@media screen and (min-width: 1700px){
  /* カードのコンテナ */
  .cards{
    margin-top: 40px;
    max-width: 1660px;
    /* gap: 縦　横; */
    gap: 60px 80px;
  }

  .card{
    padding-top: 49px;
    padding-bottom: 64px;
    padding-right: 36px;
    padding-left: 36px;

    max-width: calc(100% / 3 - 160px / 3);
    border-radius: 10px;
    box-shadow: var(--shadowPc);
    transition: border .3s;
  }

  /* 疑似要素でborderを作る PC時のみ */
  .card::before{
    content: "";
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    border: 5px solid transparent;
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    transition: all .3s;
  }
  .card:hover::before{
    border: 5px solid #39A9E7;
  }
  
  /* タイトル */
  .card__title{
    font-size: 32px;
  }

  /* 取得してきた概要、感想 */
  .card__text{
    font-size: 16px;
  }
  
  /* 概要 */
  .card__description{
    margin-top: 32px;
    margin-bottom: 8px;
    font-size: 24px;
  }
  
  /* 感想 */
  .card__comment{
    margin-top: 16px;
    margin-bottom: 8px;
    font-size: 24px;
  }
  
  /* Rating */
  .card__rating{
    margin-top: 24px;
  }

  /* React Ratingの星 */
  .card__rating svg{
    width: 22px;
    height: 22px;
  }
}


