/****************************************************************

編集モーダル

*****************************************************************/
/* 全体を覆う */
.editModal{
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .15);

  display: flex;
  justify-content: center;
  align-items: center;
}

/****************************************************************

削除モーダル

*****************************************************************/
.deleteModal{
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .15);
  display: flex;
  justify-content: center;
  align-items: center;
}

.deleteModal__block{
  padding-top: 49px;
  padding-bottom: 33px;
  padding-right: 45px;
  padding-left: 45px;
  width: 100%;
  max-width: 350px;
  background-color: var(--bgLightBeige);
  border-radius: 5px;
}

.deleteModal__title{
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: normal;
  color: var(--text);
  text-align: center;
}
.deleteModal__title span{
  font-size: 14px;
}

.deleteModal__button{
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.deleteModal__button .btn:first-child{
  margin-right: 16px;
}


@media screen and (min-width: 1660px){
  .deleteModal__block{
    padding-top: 144px;
    padding-bottom: 87px;
    padding-right: 109px;
    padding-left: 109px;
    max-width: 650px;
    background-color: var(--bgLightBeige);
    border-radius: 10px;
  }
  
  .deleteModal__title{
    font-size: 32px;
  }
  .deleteModal__title span{
    font-size: 24px;
  }
  
  .deleteModal__button{
    margin-top: 63px;
  }
  
  .deleteModal__button .btn:first-child{
    margin-right: 32px;
  }
}

